<template>
  <div>
    <canvas :width="width" :height="height" ref="stage" />
  </div>
</template>

<script>
import { handleMousedown, handlePressmove, loadImg } from "./Stage/shape";
export default {
  props: ["options"],
  data() {
    return {};
  },
  computed: {
    width() {
      return this.options.width;
    },
    height() {
      return this.options.height;
    },
  },
  async mounted() {
    var stage = new createjs.Stage(this.$refs.stage);
    var head = new createjs.Container();
    head.x = 300;
    head.y = 300;
    head.regX = 100;
    head.regY = 100;

    var egg = await loadImg(path, {});
    var mouth = await loadImg(path2);
    egg.on("mousedown", handleMousedown);
    egg.on("pressmove", handlePressmove(stage));
    createjs.Tween.get(egg, { loop: true })
      .to({ alpha: 0.5, y: 10 }, 1000, createjs.Ease.getPowInOut(2))
      .to({ alpha: 1, y: 0 }, 1000, createjs.Ease.getPowInOut(2));

    mouth.on("mousedown", handleMousedown);
    mouth.on("pressmove", handlePressmove(stage));
    head.addChild(egg);
    head.addChild(mouth);
    stage.addChild(head);
    createjs.Ticker.setFPS(40);
    createjs.Ticker.addEventListener("tick", stage);
  },
  methods: {},
};
</script>

