import {
    addFnClick,
    addFnDrag,
    loadImg
} from "./shape";
import {
    playSound,
    playSoundAndThen
} from "./sound";

var loaderBar;
var currentItem;
var stage;
var bar;
var currentImage;
var loaderWidth;
var loaderColor;
var preload;
var oldItem;

var path_egg = `static/resource/pic/egg.png`;
var path_hatch = `static/resource/pic/hatch.png`;
var path_letter_a = `static/resource/pic/letter_a.png`;
var path_mouth = `static/resource/pic/mouth.png`;
var path_right = `static/resource/pic/right.png`;
var path_wave_gif = `static/resource/pic/wave_gif.gif`;
var path_wrong = `static/resource/pic/wrong.png`;
var path_egg = `static/resource/pic/egg.png`;
var path_hatch = `static/resource/pic/hatch.png`;
var path_letter_a = `static/resource/pic/letter_a.png`;
var path_mouth = `static/resource/pic/mouth.png`;
var path_right = `static/resource/pic/right.png`;
var path_wave_gif = `static/resource/pic/wave_gif.gif`;
var path_wrong = `static/resource/pic/wrong.png`;


var canvasW = 0;
var canvasH = 0;
var scale;
var flashTime = 500;

let isHatchFirst;

let shapeMainContainer, shapeLetterA, shapeMouth, shapeHatch, shapeEgg;

let unit1, unit2;

window.disabledClick = false;

function setStage(canvasStage) {
    canvasW = canvasStage.width;
    canvasH = canvasStage.height;

    scale = {
        scaleX: canvasW / 1920,
        scaleY: canvasW / 1920,
    }
    stage = new createjs.Stage(canvasStage);
    createjs.Touch.enable(stage);
    stage.enableMouseOver(true);
    stage.mouseMoveOutside = true;
    window.onresize = detectOrient;
    detectOrient()

    function detectOrient() {
        var width = document.documentElement.clientWidth,
            height = document.documentElement.clientHeight;
        if (width >= height) {
            // 横屏
            stage.x = 0;
            stage.rotation = 0;
        } else {
            // 竖屏
            stage.x = canvasH; // 注意：x偏移相当于旋转中点处理，更简单
            stage.rotation = 90;
        }
        return [width, height];
    }

}

export async function setup(canvasStage) {
    setStage(canvasStage);

    var shapePlayButton = new createjs.Text("Click to Play", "bold  48px Arial", "#F00");
    shapePlayButton.x = shapePlayButton.y = 10;
    shapePlayButton.backgroundColor = "#ff0000";
    var shapePlayButtonRect = shapePlayButton.getBounds();

    var shapePlayButtonBG = new createjs.Shape();
    shapePlayButtonBG.cursor = "pointer";

    shapePlayButtonBG
        .graphics
        .beginFill("#000")
        .drawRect(shapePlayButtonRect.x, shapePlayButtonRect.y, shapePlayButtonRect.width + 20, shapePlayButtonRect.height + 10)
    shapePlayButtonBG.shadow = new createjs.Shadow("#000000", 5, 5, 10);
    stage.addChild(shapePlayButtonBG);


    addFnClick(shapePlayButtonBG, () => {
        shapePlayButton.visible = false;
        shapePlayButtonBG.visible = false;
        handleClickPlay();
    })
    stage.addChild(shapePlayButton)
    /*  */
    shapeMainContainer = new createjs.Container();
    addFnDrag(shapeMainContainer);
    shapeMouth = await loadImg(path_mouth);
    shapeLetterA = await loadImg(path_letter_a);
    addFnDrag(shapeLetterA);
    shapeMainContainer.addChild(shapeMouth);
    shapeMainContainer.addChild(shapeLetterA);
    addFnClick(shapeMainContainer);
    var imageContainerRect = shapeMainContainer.getBounds();
    shapeLetterA.set({
        cursor: "pointer",
        x: 80,
        y: 350
    })
    imageContainerRect = shapeMainContainer.getBounds();
    shapeMainContainer.set({
        x: ((canvasW - imageContainerRect.width) / 2),
        y: ((canvasH - imageContainerRect.height) / 2),
    });
    stage.addChild(shapeMainContainer);
    stage.addChild(loaderBar);
    createjs.Ticker.framerate = 50;
    createjs.Ticker.addEventListener("tick", stage);
}

handleClickPlay.count = 0

export async function handleClickPlay() {
    await runFlashAndSound(shapeMouth, "ha.m4a");
    createjs.Tween.get(shapeMainContainer, {
            loop: false,
        })
        .to({
            x: 0,
            ...scale
        }, flashTime, createjs.Ease.getPowInOut(2))
        .call(() => {
            const imageContainerRect = shapeMainContainer.getBounds();
            addLine(imageContainerRect);
            addSubImage();
        })

}

async function addSubImage() {
    [shapeHatch, shapeEgg] = await Promise.all([loadImg(path_hatch), loadImg(path_egg)]);

    addFnDrag(shapeHatch);
    addFnDrag(shapeEgg);

    unit1 = canvasW / 3;
    unit2 = unit1 * 2;
    isHatchFirst = ((Math.random() * 1000) % 3) - 2 > 0;

    shapeHatch.set({
        x: isHatchFirst ? unit1 : unit2,
        y: (canvasH - shapeHatch.getBounds().height) / 2
    });
    shapeEgg.set({
        x: isHatchFirst ? unit2 : unit1,
        y: (canvasH - shapeEgg.getBounds().height) / 2
    });

    addFnClick(shapeHatch, handleCorrect);
    addFnClick(shapeEgg, handleWrong);

    stage.addChild(shapeHatch);
    stage.addChild(shapeEgg);

    await pronounce();
}

async function pronounce() {
    if (isHatchFirst) {
        await runFlashAndSound(shapeHatch, "hatch.m4a")
        await runFlashAndSound(shapeEgg, "egg.m4a");
    } else {
        await runFlashAndSound(shapeEgg, "egg.m4a");
        await runFlashAndSound(shapeHatch, "hatch.m4a")
    }
}

async function runFlashAndSound(shape, sound) {
    flash(shape);
    await playSoundAndThen(sound);
}


function addLine(rect) {
    var line = new createjs.Shape();
    line.graphics.beginFill("#000").drawRect(rect.width * scale.scaleX, 0, 2, canvasH)
    line.shadow = new createjs.Shadow("#000000", 5, 5, 10);
    stage.addChild(line);
}

function handleTweenChange(tween) {
    stage.update();
}

function tweenUpComplete(event) {
    shapeMainContainer.addChildAt(currentItem, 0);
}

function handleComplete(event) {
    loaderBar.visible = false;
    stage.update();
}

function flash(shape, endFn) {
    var tw = createjs.Tween.get(shape, {
            loop: false,
        })
        .to({
            alpha: 0,
        }, flashTime, createjs.Ease.getPowInOut(2))
        .to({
            alpha: 1,
        }, flashTime, createjs.Ease.getPowInOut(2));
    endFn && tw.call(endFn)
}


async function correctMoveToCenter(shape) {
    return new Promise(resolve => {
        const shapeRect = shape.getBounds();
        createjs.Tween.get(shape, {
                loop: false,
            })
            .to({
                x: unit1 + ((unit2 - shapeRect.width) / 2)
            }, flashTime, createjs.Ease.getPowInOut(2))
            .call(resolve);
    })
}
async function handleCorrect() {
    /* call() */
    const parentShape = this;
    window.disabledClick = true;
    await runFlashAndSound(shapeHatch, "hatch.m4a")
    await Promise.all([
        addCorrectTag(parentShape),
        playSoundAndThen("ding.mp3")
    ]);
    shapeEgg.visible = false;
    await correctMoveToCenter(shapeHatch);
    await runFlashAndSound(shapeMouth, "ha.m4a");
    await runFlashAndSound(shapeMouth, "ha.m4a");
    await runFlashAndSound(shapeHatch, "hatch.m4a")
    window.disabledClick = false;

}

async function handleWrong() {
    /* call() */
    const parentShape = this;
    window.disabledClick = true;
    await Promise.all([
        addWrongTag(parentShape),
        playSoundAndThen("dong.mp3")
    ]);
    await runFlashAndSound(shapeMouth, "ha.m4a");
    await pronounce();
    window.disabledClick = false;
}

async function addWrongTag(parentShape) {
    return await addTag(addWrongTag, parentShape, path_wrong);
}

async function addCorrectTag(parentShape) {
    return await addTag(addCorrectTag, parentShape, path_right);
}

async function addTag(fn, parentShape, path) {
    if (!fn.shape) {
        fn.shape = await loadImg(path);
        addFnDrag(fn.shape);
        stage.addChild(fn.shape);
    }
    const parentShapeRect = parentShape.getBounds();
    const thisRect = fn.shape.getBounds();
    const posotioin = {
        x: parentShape.x + (parentShapeRect.width - thisRect.width) / 2,
        y: parentShape.y + ((parentShapeRect.width) + (thisRect.width / 2))
    };

    fn.shape.set(posotioin);
    fn.shape.visible = true;

    return new Promise(resolve => {
        flash(fn.shape, () => {
            fn.shape.visible = false;
            resolve();
        })
    })
}