export function loadImg(path, options) {
    return new Promise((resolve) => {
        var bmpImg = new createjs.Bitmap(path);
        if (options) {
            bmpImg.set(options)
        }
        bmpImg.image.onload = () => resolve(bmpImg);
        // bmpImg.filters = [new createjs.ColorFilter(0, 0, 0, 1, 0, 0, 255, 1)];
        // bmpImg.cache(0, 0, 200, 200);
    });
}



export function handleMousedown(evt) {
    // keep a record on the offset between the mouse position and the container
    // position. currentTarget will be the container that the event listener was added to:
    evt.currentTarget.offset = {
        x: this.x - evt.stageX,
        y: this.y - evt.stageY
    };

}

export function addFnDrag(shap, stage) {
    if (process.env.NODE_ENV !== "production") {
        shap.on("mousedown", handleMousedown);
        shap.on("pressmove", handlePressmove(stage));
    }
}

export function addFnClick(shap, fn) {
    shap.on("click", evt => {
        if (window.disabledClick) return;
        if (process.env.NODE_ENV !== "production") {
            console.log('shap', shap, shap.getBounds());
            console.log('evt.currentTarget.parent', evt.currentTarget.parent.getBounds());
            console.log('evt.currentTarget', evt.currentTarget.getBounds());
        }
        fn && fn.call(shap, evt)
    });
}


export function handlePressmove(stage) {
    return function (evt) {
        // Calculate the new X and Y based on the mouse new position plus the offset.
        evt.currentTarget.x = evt.stageX + evt.currentTarget.offset.x;
        evt.currentTarget.y = evt.stageY + evt.currentTarget.offset.y;
        // make sure to redraw the stage to show the change:
        console.log('evt.currentTarget.parent', evt.currentTarget.parent.getBounds());
        console.log('evt.currentTarget', evt.currentTarget.getBounds());
        stage && stage.update();
    }
}