<template>
  <div>
   <!--  <button @click="play" class="playSound">Play</button>
    <button
      @click="playSound(name)"
      class="playSound"
      v-for="name in soundContent"
      :key="name"
    >
      {{ name }}
    </button>
    <button
      @click="playSound(name)"
      class="playSound"
      v-for="name in soundUtil"
      :key="name"
    >
      {{ name }}
    </button> -->
    <canvas :width="width" :height="height" ref="stage" />
  </div>
</template>

<script>
import { setup, handleClickPlay } from "./Stage/setup";
import { playSound, soundContent, soundUtil } from "./Stage/sound";

export default {
  props: ["options"],
  data() {
    return {
      soundContent,
      soundUtil,
    };
  },
  computed: {
    width() {
      return this.options.width;
    },
    height() {
      return this.options.height;
    },
  },
  async mounted() {
    const canvasStage = this.$refs.stage;
    await setup(canvasStage);
  },
  methods: {
    playSound,
    play() {
      handleClickPlay();
    },
  },
};
</script>

<style>
.playSound {
  /* float: left; */
  z-index: 1;
}
canvas {
  /* background: gray; */
}
</style>
