import _ from "lodash";

var soundID = "Thunder";
/* */

const soundPrefixContent = "static/resource/audio/m4a/";
const oundPrefixUtil = "static/resource/audio/mp3/";
export const soundContent = ["egg.m4a", "ha.m4a", "hatch.m4a", "intro1.m4a", "intro3.m4a", "sound_a.m4a"];
export const soundUtil = ["ding.mp3", "dong.mp3"];


function registerSound(path, soundContent) {
    const soundID = _.camelCase(soundContent);
    createjs.Sound.registerSound(path + soundContent, soundID);
    return soundID;
}


soundContent.forEach(soundContent => registerSound(soundPrefixContent, soundContent));
soundUtil.forEach(soundContent => registerSound(oundPrefixUtil, soundContent));

export function playSound(soundContent) {
    const soundID = _.camelCase(soundContent);
    return createjs.Sound.play(soundID);
}

export async function playSoundAndThen(soundContent) {
    // return Promise.resolve();
    const soundID = _.camelCase(soundContent);
    return new Promise((resolve) => createjs.Sound.play(soundID).on("complete", (event) => resolve(event)))
}

