<template>
  <div id="app" :style="styleScale">
    <Stage :options="{ width, height }" v-if="isShowStage" />
  </div>
</template>

<script>
import Stage from "./module/Stage";
import StageBitmap from "./module/StageBitmap";

export default {
  components: {
    Stage,
    StageBitmap,
  },
  data() {
    return {
      width: 500,
      height: 300,
      isShowStage: false,
      scale: 1,
    };
  },
  name: "App",
  computed: {
    styleScale() {
      return { transform: `{transform:'scale(${this.scale})'}` };
    },
  },
  mounted() {
    var width = document.documentElement.clientWidth,
      height = document.documentElement.clientHeight;

    this.width = width;
    this.height = height;
    this.scale = width / 1920;
    this.$nextTick(() => {
      console.log("App", width, height);
      this.isShowStage = true;
    });
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
</style>
